.wrapperLoader {
display: flex;
align-items: center;
justify-content: center;
height: 266px;

@media screen and (min-width: 768px) {
        height: 282px;
    }
}


.spinner {
    position: relative;
    width: 60px;
    height: 60px;
    border-radius: 50%;
}

.spinner::before,
.spinner::after {
    content: "";
    position: absolute;
    border-radius: inherit;
}

.spinner::before {
    width: 100%;
    height: 100%;
    background-image: linear-gradient(0deg, #3E85F3 0%, transparent 50%);
    animation: spin .5s infinite linear;
}

.spinner::after {
    width: 85%;
    height: 85%;
    background-color: rgb(23, 24, 32);
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}




// 
.check-animation svg {
    stroke-dasharray: 50;
    stroke-dashoffset: 50;
    animation: drawCheck 1.5s forwards;
}

@keyframes drawCheck {
    to {
        stroke-dashoffset: 0;
    }
}